<template>
	<div class="propertypay">
		<div class="propertypay-name">
			<span class="name">时代玫瑰园一期-5栋-3单元-1314</span>
			<span class="price">应缴费：<span>¥{{allprice}}</span></span>
		</div>

		<ul class="propertypay-list">
			<li class="propertypay-list-li" v-for="(item, index) in list" :key="index" :title="item">
				<md-agree
					v-model="agreeConf['checked_'+index]"
					:disabled="agreeConf.disabled"
					:size="agreeConf.size"
					@change="onChange(['agree_'+index], agreeConf['checked_'+index], index)"
				>
					<ul class="show-list">
						<li>
							<span>{{item.time}}</span>
							<span>¥{{item.allprice}}</span>
						</li>
						<li>
							<span>物业费</span>
							<span>¥{{item.wyprice}}</span>
						</li>
						<li>
							<span>公摊水费</span>
							<span>¥{{item.waterprice}}</span>
						</li>
					</ul>
				</md-agree>
			</li>
		</ul>
		<div class="propertypay-bottom">
			<div class="all-price">总计<span>¥{{allprice}}</span></div>
			<md-button class="pay-btn" type="primary" @click="succeedConfirm">立即缴费</md-button>
		</div>
	</div>
</template>

<script>
import { Agree, Dialog } from 'mand-mobile'

export default {
	name:'fee',
	components: {
		[Agree.name]: Agree,
		[Dialog.name]: Dialog
	},
	data () {
		return {
			successDialog: {
				open: false,
				btns: [
					{
						text: '确认操作',
						handler: this.onIconConfirm,
					},
				],
			},
			agreeConf: {
				checked_0: true,
				checked_1: true,
				size: 'md',
				disabled: false,
				introduction: '选中状态',
			},
			allprice: '600',
			list: [
			{
				time: "2020年2月5日",
				allprice: '300',
				wyprice: '200',
				waterprice: '100'
			},{
				time: "2020年3月5日",
				allprice: '300',
				wyprice: '200',
				waterprice: '100'
			},{
				time: "2020年4月5日",
				allprice: '400',
				wyprice: '200',
				waterprice: '200'
			},{
				time: "2020年5月5日",
				allprice: '280',
				wyprice: '200',
				waterprice: '80'
			},{
				time: "2020年6月5日",
				allprice: '400',
				wyprice: '200',
				waterprice: '200'
			},{
				time: "2020年7月5日",
				allprice: '400',
				wyprice: '200',
				waterprice: '200'
			},{
				time: "2020年8月5日",
				allprice: '400',
				wyprice: '200',
				waterprice: '200'
			}],
		}
	},
	methods: {
		onChange(name, checked,index) {

			if(checked){
				this.allprice = parseInt(this.allprice) + parseInt(this.list[index].allprice);
			}else{
				this.allprice = parseInt(this.allprice) - parseInt(this.list[index].allprice);
			}
			
			console.log(checked)
			console.log(`agree name = ${name} is ${checked ? 'checked' : 'unchecked'}`)
		},

		succeedConfirm() {
			Dialog.succeed({
				title: '缴费成功',
				cancelText: '取消',
				confirmText: '确定',
				onConfirm: () => console.log('[Dialog.succeed] confirm clicked'),
			})
		},
		
	},
}
</script>


